  --spacing-xxxl: 80px;
  --color-white: var(--wp--preset--color--white);
  --color-ui-background-01: var(--wp--preset--color--ui-background-01);
  --color-ui-background-02: var(--wp--preset--color--ui-background-02);
  --color-ui-01: var(--wp--preset--color--ui-01);
  --color-ui-02: var(--wp--preset--color--ui-02);
  --color-black: var(--wp--preset--color--black);
  --color-ui-03: var(--wp--preset--color--ui-03);
  --color-ui-04: var(--wp--preset--color--ui-04);
