  text-transform: uppercase;
}

a {
  text-decoration: none;
  color: inherit;
}

p a {
  text-decoration: underline;
}

.wp-block-media-text {
  --media-text-gutter: 126px;
}
> .wp-block-media-text__content {
  align-self: flex-start;
  padding-right: 0;
  padding-left: var(--media-text-gutter);
}
.wp-block-media-text.has-media-on-the-right > .wp-block-media-text__content {
  padding-left: 0;
  padding-right: var(--media-text-gutter);
}
.wp-block-media-text.has-background .wp-block-media-text__content {
  padding: 100px 90px;
}

.wp-block-media-text.is-style-hero {
  box-sizing: border-box;
  max-width: var(--alignwide-width);
  padding-left: var(--gutter);
  padding-right: var(--gutter);
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.wp-block-media-text.is-style-hero .wp-block-media-text__content {
  padding-right: 0;
  padding-left: var(--media-text-gutter);
}
.wp-block-media-text.is-style-hero.has-media-on-the-right > .wp-block-media-text__content {
  padding-left: 0;
  padding-right: var(--media-text-gutter);
}
.wp-block-media-text.is-style-hero::before {
  display: block;
  content: "";
  position: absolute;
  background-color: inherit;
  top: 0;
  bottom: 0;
  left: calc(-50vw + 50%);
  width: 100vw;
  z-index: -1;
}

.wp-block-media-text.is-style-two-thirds {
  --media-text-two-thirds: calc(100% / 3 * 2);
  grid-template-columns: 1fr var(--media-text-two-thirds) !important;
}
.wp-block-media-text.is-style-two-thirds.has-media-on-the-right {
  grid-template-columns: var(--media-text-two-thirds) 1fr !important;
}

.wp-block-media-text.is-image-fill figure.wp-block-media-text__media > img {
  position: relative;
  margin: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.wp-block-media-text.has-background {
  --tag-background-color: transparent;
  --tag-border-color: white;
}

.wp-block-tag {
  display: inline-block;
  color: var(--tag-color, white);
