.column {
  margin-bottom: var(--grid-gutter-y);
}

.teaser--case, .teaser--post {
  --gds-card-width: 100%;
}

.is-root-container {
  padding-left: var(--gutter);
  padding-right: var(--gutter);
}
.is-root-container > [data-align=full] {
  max-width: var(--alignfull-width);
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
  width: auto;
}

.wp-block {
  max-width: var(--content-width);
}
.wp-block:first-child {
  margin-top: 0;
}
.wp-block:last-child {
  margin-bottom: 0;
}
.wp-block[data-align=wide] {
  max-width: var(--alignwide-width);
}
.wp-block[data-align=full] {
  max-width: var(--alignfull-width);
}

[data-align=wide],
[data-align=full],
.alignwide,
.alignfull {
  --block-gutter: var(--block-gutter-lg);
}

.alignfull + .alignfull,
[data-align=full] + [data-align=full] {
  margin-top: calc(var(--block-gutter-lg) * -1);
}

.acf-block-preview [href]:not([href^="#"]) {
  pointer-events: none;
}