@mixin base {
  display: block;
  font-family: var(--heading-font-family);
  font-weight: 600;
  letter-spacing: 0px;
}

// H1
@mixin size-xxl {
  @include base;
  font-size: 72px;
  line-height: 74px;
}

// H2
@mixin size-xl {
  @include base;
  font-size: 60px;
  line-height: 60px;
}

// H3
@mixin size-l {
  @include base;
  font-size: 36px;
  line-height: 36px;
}

// H4
@mixin size-m {
  @include base;
  font-size: 28px;
  line-height: 28px;
}

// H5
@mixin size-s {
  @include base;
  font-size: 20px;
  line-height: 24px;
  font-weight: 800;
  letter-spacing: 1.6px;
  text-transform: uppercase;
}
